import refreshUiRecipes from "./components/recipesUI";
import { allRecipes } from "./http";
import { arrayNoDuplicates, initialState } from "./services";
import domBuilder from "./domBuilder";
import createTag from "./components/tag";
import {
  updateOneSelect,
  updateAllSelects,
  elementsInSelect,
} from "./components/filterSelect/updateSelect";

const search = () => {
  const inputMain = document.querySelector("[name='q']");

  let allTags = {
    ingredients: [],
    appareils: [],
    ustensiles: [],
  };
  let mainResults = [];

  /**
   * @param {Array} searchedText
   */
  const searchAndUpdateResult = (searchedText) => {
    const recipes = findRecipesByMain(searchedText);
    /*Actualisation de l'interfacce */
    refreshUiRecipes(recipes);
    updateAllSelects(recipes);
  };

  /*Check si il ya des tags de recherche affichs*/
  const isTag = () => {
    if (
      allTags.ingredients.length > 0 ||
      allTags.appareils.length > 0 ||
      allTags.ustensiles.length > 0
    ) {
      return true;
    }
  };

  /*Return un array de recipes filtr avec la recherche principale */
  const findRecipesByMain = (searchedText) => {
    let recipes = [];
    let resultsFromMain = [];

    /*Si il y a des tags on prend comme tableau de recherches les recettes dj filtres par un ou des tags */
    if (isTag()) {
      recipes = findRecipesByTags();
    } /*Sinon on prend toutes les recettes */ else {
      recipes = allRecipes;
    }
    /*On boucle sur la liste recipes */
    for (let i = 0; i < recipes.length; i++) {
      let allTerms = new Array(searchedText.length);

      /*On boucle sur un tableau de termes pour grer une recherche  plusieurs termes */
      for (let j = 0; j < searchedText.length; j++) {
        /*Si le terme[j] est prsent dans le nom de la recette on le passe  true */
        if (recipes[i].name.toLowerCase().includes(searchedText[j])) {
          allTerms[j] = true;
        }
        /*Si le terme[j] est prsent dans la description on le passe  true */
        if (recipes[i].description.toLowerCase().includes(searchedText[j])) {
          allTerms[j] = true;
        }
        /*Si le terme[j] est prsent dans la liste des ingrdients de la recette on le passe  true */
        for (let k = 0; k < recipes[i].ingredients.length; k++) {
          if (
            recipes[i].ingredients[k].ingredient
              .toLowerCase()
              .includes(searchedText[j])
          ) {
            allTerms[j] = true;
          }
        }
      }

      /*On compte le nombre de terms gal  true */
      let countPresentTerm = 0;
      for (let k = 0; k < allTerms.length; k++) {
        if (allTerms[k]) {
          countPresentTerm++;
        }
      }
      /*Si le nombre de true est gal aux nombres de termes recherchs tous les termes sont prsents dan sla recette et on ajoute la recette au rsultats*/
      if (allTerms.length === countPresentTerm) {
        arrayNoDuplicates(resultsFromMain, recipes[i]);
      }
    }
    if (resultsFromMain.length === 1) {
      emptySelects();
    }
    return (mainResults = resultsFromMain);
  };

  /*La recherche ne se dclenche qu' partir de 3 chars saisis */
  const canSearch = (searchedText) => {
    return searchedText.length > 2;
  };

  /*Rinitialisation des rsultats quand on efface les termes de recherche */
  const reset = (searchedText) => {
    return searchedText[0] === "";
  };

  const emptySelects = () => {
    updateAllSelects([]);
  };

  const onSearch = (e) => {
    console.log(e);
    /*La recherche est un tableau de termes pour prendre en compte plusieurs mot et boucler dessus */
    const searchedText = e.target.value.toLowerCase().split(" ");

    if (canSearch(searchedText[0])) {
      domBuilder.removeElements(document.querySelectorAll(".recettes article"));
      searchAndUpdateResult(searchedText);
    }

    /*Quand l'utilisateur efface les termes de la recherche principale*/
    if (reset(searchedText)) {
      /*Si il y a des tags prsents on rafrachit les rsultats avec les rsultats de recherche de ces tags*/
      if (isTag()) {
        findRecipesByTags();
      } /*Sinon on affiche toutes les recttes */ else {
        initialState();
      }
    }
  };

  inputMain.addEventListener("input", (e) => onSearch(e));
  inputMain.addEventListener("keypress", (e) => {
    if (e.code === "Enter") {
      e.preventDefault();
    }
  });

  /*TAG */

  /**
   * Filtre la liste des select en temps rel suivant les termes recherchs
   * @param {Array} searchedTag
   * @param {string} selectType
   */
  const liveRefreshOptionsSelect = (searchedTag, selectType) => {
    /*Si il a une recherche princpale d'ffectue on utilise ces rsultats comme base de recherche sinon on utilise toutes les recettes*/
    const currentResults = mainResults.length === 0 ? allRecipes : mainResults;

    /*Dclaration du tableau de tous les lments de liste qui match avec la recherche*/
    let liInSelect = [];

    /**
     * On cre une fonction qui check si le tag est prsent dans la liste d'un select
     * @param {string} select
     */
    const getTagsInSelect = (select) => {
      select.forEach((li) => {
        /*On cre un tableau pour grer une rechrche avec plusieurs termes*/
        let isInList = [];

        /*Pou chaque terme de recherche */
        for (let i = 0; i < searchedTag.length; i++) {
          /*Si le terme est prsent dans un lment de liste*/
          if (li.includes(searchedTag[i])) {
            /*On l'ajoute au tableau*/
            isInList.push(li);
          }
        }
        /*Si le nombre de termes de la recherche est gal au nombre de terme dans le tableau c'est que tous les temres sont dans l'lement de liste, on peut ajouter l'lment au tableau de tous les lments */
        if (searchedTag.length === isInList.length) {
          arrayNoDuplicates(liInSelect, li);
        }
      });
      return liInSelect;
    };

    /*On met  jour les lements de liste du select concern */
    switch (selectType) {
      case "ingredients":
        updateOneSelect(
          selectType,
          getTagsInSelect(elementsInSelect.ingredients(currentResults))
        );
        break;
      case "appareils":
        updateOneSelect(
          selectType,
          getTagsInSelect(elementsInSelect.appareils(currentResults))
        );
        break;
      case "ustensiles":
        updateOneSelect(
          selectType,
          getTagsInSelect(elementsInSelect.ustensiles(currentResults))
        );
        break;
    }
  };

  /*Recherhce par tags */
  const findRecipesByTags = () => {
    /*On prend comme base de recherche les rsultas de la rechercheprincipale s'il y en a une sinon toutes les recettes*/
    let filteredFromTags = mainResults.length > 0 ? mainResults : allRecipes;

    /*On filtre les rsultats avec les tags prsents */
    if (allTags.ingredients.length > 0) {
      allTags.ingredients.forEach((tag) => {
        filteredFromTags = filteredFromTags.filter((recipe) =>
          recipe.containsText(
            tag.replaceAll("-", " "),
            recipe.searchInIngredients
          )
        );
      });
    }

    if (allTags.appareils.length > 0) {
      allTags.appareils.forEach((tag) => {
        filteredFromTags = filteredFromTags.filter((recipe) =>
          recipe.containsText(
            tag.replaceAll("-", " "),
            recipe.searchInAppareils
          )
        );
      });
    }

    if (allTags.ustensiles.length > 0) {
      allTags.ustensiles.forEach((tag) => {
        filteredFromTags = filteredFromTags.filter((recipe) =>
          recipe.containsText(
            tag.replaceAll("-", " "),
            recipe.searchInUstensiles
          )
        );
      });
    }
    /*On update l'UI (recettes)  et les listes des selects*/

    refreshUiRecipes(filteredFromTags);
    updateAllSelects(filteredFromTags);

    if (filteredFromTags.length === 1) {
      emptySelects();
    }
    return filteredFromTags;
  };

  /*Dclenche la recherche par tag quand un tag est valid */
  const onValidateTag = (e, input) => {
    if (e.code === "Enter") {
      e.preventDefault();
      const select = input.getAttribute("name");

      createTag(input.value, select).addEventListener("click", (e) =>
        closeTag(e)
      );

      allTags[input.getAttribute("name")].push(
        input.value.replaceAll(" ", "-")
      );

      input.value = null;
    }
    if (!e.code) {
      const select = e.target.getAttribute("id").replace(/-[0-9]?[0-9]/, "");
      const tag = e.target.textContent;
      createTag(tag, select).addEventListener("click", (e) => closeTag(e));

      allTags[select].push(tag.replaceAll(" ", "-"));
    }
    findRecipesByTags();
  };

  /*A la fermeture d'un tag on supprime le tag de l'interface et du tableau des tags et on update les listes des selects */
  const closeTag = (e) => {
    const select = e.currentTarget.getAttribute("data-select");
    const tag = e.currentTarget.getAttribute("data-tag");

    document.querySelector(`.tag-${tag}`).remove();
    allTags[select] = allTags[select].filter((value) => value !== tag);

    updateAllSelects(findRecipesByTags());

    if (isTag()) {
      findRecipesByTags();
    } else if (inputMain.value !== "") {
      searchAndUpdateResult(inputMain.value.toLowerCase().split(" "));
    }
  };

  /*Ecouteurs sur les input de tags */
  const tagsInput = [
    document.querySelector("[name='ingredients']"),
    document.querySelector("[name='ustensiles']"),
    document.querySelector("[name='appareils']"),
  ];

  tagsInput.forEach((tagInput) => {
    /*Ecouteur de la saisi des tags*/
    tagInput.addEventListener("input", (e) => {
      const searchedTag = e.target.value.toLowerCase().split(" ");
      const selectType = e.target.getAttribute("name");
      liveRefreshOptionsSelect(searchedTag, selectType);
    });
    /*Ecouteurs pour validation du tag Enter key*/
    tagInput.addEventListener("keydown", (e) => {
      if (e.code === "Enter") {
        onValidateTag(e, tagInput);
      }
    });
  });

  /*Selects updates */

  document.querySelectorAll(".combo-list li").forEach((li) => {
    li.addEventListener("click", (li) => {
      const selectInput = document.getElementsByName(
        li.target.getAttribute("id").replace(/-[0-9]?[0-9]/, "")
      )[0];
      selectInput.value = li.target.textContent;
      selectInput.focus();
    });
  });
};

export default search;
